.co {
  font-family: 'Cinzel', serif;
  font-size: 3rem;
  margin: 0px 5px;
  padding-top: 30px;
  cursor: pointer;
  animation: lightning 2s infinite;
}
.year {
  font-family: 'Cinzel', serif;
  font-size: 3rem;
  margin-top: -20px;
}
.footer-section p {
  margin-top: 7px;
}
.sub {
  font-size: large;
  font-weight: 400;
}
.text-sm {
  font-size: small;
}

.hompage .team-cards-container {
  margin-top: 50px;
}
.hompage .team-card {
  height: auto;
  width: 70%;
}
.hompage .team-card:hover {
  height: auto;
}
.tilak {
  text-align: center;
  margin-top: 70px;
}
.tilaks {
  font-size: 35px;
  font-weight: 450;
  background: linear-gradient(
    90deg,
    #4873ff 40%,
    #d148d3 60%
  ); /* More distinct color transition */
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.spo {
  display: flex;
  justify-content: center;
  gap: 35px;
}
.team-card {
  height: 90%;
  padding: 5px 25px;
  /* margin:0px 10px; */
}

.teams-section h3 {
  margin-top: 0px;
}
.converse .heading {
  background: linear-gradient(
    90deg,
    #4873ff 40%,
    #d148d3 60%
  ); /* More distinct color transition */
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-weight: 450;
}
.converse p {
  font-weight: 450;
}

@media screen and (max-width: 669px) {
  .spo {
    flex-direction: column;
  }
}

@media screen and (min-width: 900px) {
  .co {
    font-size: 5rem;
  }
  .year {
    font-size: 4rem;
  }
}

.button-style {
  /* From https://css.glass */
  display: flex;
  align-items: center;
  gap: 16px;
  justify-content: center;
  width: 246px;
  background: rgba(134, 134, 134, 0.32);
  border-radius: 16px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(5.4px);
  -webkit-backdrop-filter: blur(5.4px);
  padding: 10px 40px;
  border-top: 1.5px solid #4873ff;
  border-bottom: 1.5px solid #d148d3;
  border-left: 1.5px solid #4873ff;
  border-right: 1.5px solid #d148d3;
}
